import React, {
	useState,
	useEffect,
} from "react";
import "./style/App.css";
import { GiShoppingBag } from "react-icons/gi";
import ShoppingCart from "./Components/ShoppingCart";

const products = [
	{
		id: 1,
		name: "Huppari",
		description: "Erittäin laadukkaalla materiaalilla tehty musta huppari.",
		price: 50,
		image: require("./images/hoodie.png"),
	},
	{
		id: 2,
		name: "Lippis",
		description: "Erittäin laadukas musta lippis!",
		price: 20,
		image: require("./images/cap.png"),
	},
	{
		id: 3,
		name: "T-paita",
		description: "Kirkkaan valkoinen T-paita.",
		price: 15,
		image: require("./images/t-shirt.png"),
	},
	{
		id: 4,
		name: "Kengät",
		description: "Kirkkaan valkoiset kengät.",
		price: 40,
		image: require("./images/shoes.png"),
	},

];

function App() {
	function clearCart() {
		setProducts([]);
	  }
	const [cartsVisibilty, setCartVisible] =
		useState(false);
	const [productsInCart, setProducts] =
		useState(
			JSON.parse(
				localStorage.getItem(
					"shopping-cart"
				)
			) || []
		);

	useEffect(() => {
		localStorage.setItem(
			"shopping-cart",
			JSON.stringify(productsInCart)
		);
	}, [productsInCart]);
	const addProductToCart = (product) => {
		const newProduct = {
			...product,
			count: 1,
		};
		setProducts([
			...productsInCart,
			newProduct,
		]);
	};

	const onQuantityChange = (
		productId,
		count
	) => {
		setProducts((oldState) => {
			const productsIndex =
				oldState.findIndex(
					(item) =>
						item.id === productId
				);
			if (productsIndex !== -1) {
				oldState[productsIndex].count =
					count;
			}
			return [...oldState];
		});
	};

	const onProductRemove = (product) => {
		setProducts((oldState) => {
			const productsIndex =
				oldState.findIndex(
					(item) =>
						item.id === product.id
				);
			if (productsIndex !== -1) {
				oldState.splice(productsIndex, 1);
			}
			return [...oldState];
		});
	};

	return (
		<div className="App">
			<ShoppingCart
				visibilty={cartsVisibilty}
				products={productsInCart}
				clearCart={clearCart}
				setCartVisible={setCartVisible} 
				onClose={() =>
					setCartVisible(false)
				}
				onQuantityChange={
					onQuantityChange
				}
				onProductRemove={onProductRemove}
			/>
			<div className="navbar">
				<h3 className="logo">Matiaksen fanikauppa</h3>
				<button
					className="btn shopping-cart-btn"
					onClick={() =>
						setCartVisible(true)
					}>
					<GiShoppingBag size={24} />
					{productsInCart.length >
						0 && (
						<span className="product-count">
							{
								productsInCart.length
							}
						</span>
					)}
				</button>
			</div>
			<main>
				<h2 className="title">Tuotteet</h2>
				<div className="products">
					{products.map((product) => (
						<div
							className="product"
							key={product.id}>
							<img
								className="product-image"
								src={
									product.image
								}
								alt={
									product.image
								}
							/>
							<p>
								{
									product.description
								}
							</p>
							<span className="product-price">
								{product.price}€
							</span>
							<div className="buttons">
								<button
									className="btn"
									onClick={() =>
										addProductToCart(
											product
										)
									}>
									Lisää ostoskoriin
								</button>
							</div>
						</div>
					))}
				</div>
			</main>
		</div>
	);
}

export default App;