import React, { useState, useEffect } from "react";
import "../style/ShoppingCart.css";
import { AiFillCloseCircle } from "react-icons/ai";
import { RiDeleteBin6Line } from "react-icons/ri";

function ShoppingCart({
  visibilty,
  products={products},
  onProductRemove,
  onClose,
  onQuantityChange,
  setCartVisible,
  clearCart,
  
}) {
  const [formVisible, setFormVisible] = useState(false);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [zipCode, setZipCode] = useState("");
  const [city, setCity] = useState("");
  const [isFormFilled, setIsFormFilled] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    // muista lisätä kuitti
    alert(
      "Kiitos paljon tilauksestasi " +
        name +
        "! Tilaamasi tuotteet toimitetaan 2-5 arkipäivän kuluessa osoitteeseen: " +
        address
    );
    setName("");
    setEmail("");
    setPhone("");
    setAddress("");
    setZipCode("");
    setCity("");
    setFormVisible(false);
    setCartVisible([]);
    clearCart();
    onClose();
  };
  useEffect(() => {
    const isFilled = name && email && phone && address && zipCode && city;
    setIsFormFilled(isFilled);
  }, [name, email, phone, address, zipCode, city]);
  return (
    <div
      className="modal"
      style={{
        display: visibilty ? "block" : "none",
      }}
    >
      <div className="shoppingCart">
        <div className="header">
          <h2>Ostoskori</h2>
          <button className="btn close-btn" onClick={onClose}>
            <AiFillCloseCircle size={30} />
          </button>
        </div>
        <div className="cart-products">
        {products.length === 0 && (
						<span className="empty-text">
              Ostoskorisi on tällä hetkellä tyhjä
						</span>
          )}
          {products.map((product) => (
            <div className="cart-product" key={product.id}>
              <img src={product.image} alt={product.name} />
              <div className="product-info">
                <h3>{product.name}</h3>
                <span className="product-price">
                  {product.price * product.count}€
                </span>
              </div>
              <select
                className="count"
                value={product.count}
                onChange={(event) => {
                  onQuantityChange(product.id, event.target.value);
                }}
              >
                {[...Array(10).keys()].map((number) => {
                  const num = number + 1;
                  return (
                    <option value={num} key={num}>
                      {num}
                    </option>
                  );
                })}
              </select>
              <button
                className="btn remove-btn"
                onClick={() => onProductRemove(product)}
              >
                <RiDeleteBin6Line size={20} />
              </button>
            </div>
          ))}
          {products.length > 0 && !formVisible && (
            <button
              className="btn checkout-btn"
              onClick={() => setFormVisible(true)}
            >
              Siirry ostoskoriin
            </button>
          )}
{formVisible && (
  <form onSubmit={handleSubmit}>
    <div className="shoppingCart-checkout">
      <div className="checkout-window">
      <h3>Olet ostamassa seuraavia tuotteita:</h3>
      {products.map((product) => (
        <div className="" key={product.id}>
          <div className="product-price">
            <h3>
              {product.name} {product.count}kpl
            </h3>
            <span className="product-info">
              {product.price * product.count}€
            </span>
          </div>
        </div>
      ))}
      <h3>
        Tuotteiden kokonaishinta:{" "}
        {products.reduce((sum, product) => sum + product.price * product.count, 0)}€
      </h3>
      <div className="form-row">
        <label htmlFor="name">Koko nimi</label>
        <input
          type="text"
          id="name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
      </div>
      <div className="form-row">
        <label htmlFor="email">Sähköpostiosoite</label>
        <input
          type="email"
          id="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
      </div>
      <div className="form-row">
        <label htmlFor="phone">Puhelinnumero</label>
        <input
          type="phone"
          id="phone"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          required
        />
      </div>
      <div className="form-row">
        <label htmlFor="phone">Kotiosoite</label>
        <input
          type="address"
          id="address"
          value={address}
          onChange={(e) => setAddress(e.target.value)}
          required
        />
      </div>
      <div className="form-row">
        <label htmlFor="phone">Postinumero</label>
        <input
          type="zipCode"
          id="zipCode"
          value={zipCode}
          onChange={(e) => setZipCode(e.target.value)}
          required
        />
      </div>
      <div className="form-row">
        <label htmlFor="phone">Postitoimipaikka</label>
        <input
          type="city"
          id="city"
          value={city}
          onChange={(e) => setCity(e.target.value)}
          required
        />
          </div>
        <button  type="submit" className="confirm-btn" disabled={!isFormFilled} onClick={() => setCartVisible(false)}>Vahvista tilaus</button>
      </div>
    </div>
  </form>
)}
		  	</div>
		</div>
	</div> )}
export default ShoppingCart;